import emailjs from '@emailjs/browser';
import { useState } from 'react';

export const formSubmitErrorMessage = 'Oops! Something went wrong, please try again later or contact us if the problem persists';

export function sendEmail({
  templateId,
  form,
  successCallback,
  errorCallback,
}) {
  const serviceId = process.env.GATSBY_EMAILJS_SERVICE_ID;
  const publicKey = process.env.GATSBY_EMAILJS_PUBLIC_KEY
  emailjs.sendForm(
    serviceId!,
    templateId,
    form,
    publicKey,
  ).then(
    (result) => successCallback(result),
    () => errorCallback(),
  );
}

export function useSendEmail({
  templateId,
  successCallback,
  errorCallback,
}: {
  templateId: string;
  successCallback?: () => void;
  errorCallback?: () => void; 
}) {
  const serviceId = process.env.GATSBY_EMAILJS_SERVICE_ID;
  const publicKey = process.env.GATSBY_EMAILJS_PUBLIC_KEY
  const [submitting, setSubmitting] = useState(false)
  const [status, setStatus] = useState<null|'success'|'error'>();

  function send(form) {
    setSubmitting(true);
    emailjs.sendForm(
      serviceId!,
      templateId,
      form,
      publicKey,
    ).then(
      () => {
        setStatus('success')
        if (successCallback) {
          (successCallback as Function)();
        }
      },
      () => {
        setStatus('error')
        if (errorCallback) {
          (errorCallback as Function)();
        }
      },
    ).finally(() => setSubmitting(false));
  }

  return {
    error: status === 'error',
    send,
    submitting,
    success: status === 'success',
  }
}