import React from 'react'

function FormErrorMessages({
  errors,
}) {
  const errorsKeys = Object.keys(errors);

  if (errorsKeys.length === 0) {
    return null;
  }

  return (
    <div className="border-2 border-danger text-danger p-4 mb-4">
      {errorsKeys.map(errorKey => (
        <p key={`error-${errorKey}`}>{errors[errorKey].message}</p>
      ))}
    </div>
  )
}

export default FormErrorMessages